import { Component, OnInit } from '@angular/core';
import { LocalStorageService } from "ngx-webstorage";
import { AuthService } from "../../../login/services/auth.service";
@Component({
  selector: 'cbas-header',
  templateUrl: 'cbas-header.component.html',
  styleUrls: ['./cbas-header.component.scss']
})

export class CbasHeaderComponent implements OnInit {


  public isUserLoggedIn: boolean;
  public loggedInUserName: string;

  constructor(
    public auth: AuthService,
    private storage: LocalStorageService, ) { }

  ngOnInit() {
  }


  public onClickLogout() {
    console.log("logout");

    this.auth.jwt_logout();
    this.storage.clear("LOGGEDUSERID");
    this.storage.clear("LOGGEDUSEREMAIL");
    this.storage.clear("SearchType");
    this.storage.clear("SearchResource");
    this.isUserLoggedIn = this.isLoggedIn();
    this.loggedInUserName = undefined;
  }

  public isLoggedIn() {
    console.log("is log");

    // let status: LoginState = this.auth.amp_getLoginState();
    // if (this.auth.jwt_loggedIn()) {
    // 	return true;
    // }
    // return false;

    return this.auth.jwt_loggedIn() ? true : false;
  }

  public emailCBASAdmin(){
    console.log("Sent email to CBASE Admin");
  }

  navigateToHyperion() {
    window.open("http://ussltcovm668.solutions.glbsnet.com:19000/workspace/index.jsp","_blank", "toolbar=yes,scrollbars=yes,resizable=yes,top=500,left=500,width=400,height=400");
  }

}