import { Routes } from "@angular/router";
import { ErrorComponent } from "./error/error.component";
import { SubmitPlanComponent } from "./components/submit-plan/submit-plan.component";
import { CurrentRequestReportsComponent } from "./components/current-request-reports/current-request-reports.component";
import { ApproveSpendPlanComponent } from "./components/approve-spend-plan/approve-spend-plan.component";
import { AcquistionsFyTwentyComponent } from "./components/acquistions-fy-twenty/acquistions-fy-twenty.component";

export const ROUTES: Routes = [
	{
		path: "",
		loadChildren: "./login/login.module#LoginModule"
	},
	{
		path: "app",
		loadChildren: "./layout/layout.module#LayoutModule"
	},
	{
		path:'app/change-request/office',
		component: SubmitPlanComponent
	},
	// Jaswanthi
	{
		path:'app/acquistions-fy20',
		component: AcquistionsFyTwentyComponent
	},
	{
		path: 'app/reports/current-request-reports',
		component: CurrentRequestReportsComponent
	},
	{
		path: 'app/approvePlan/approve-submit-plan',
		component: ApproveSpendPlanComponent
	},
	{
		path: "error",
		component: ErrorComponent
	},
	{
		path: "**",
		component: ErrorComponent
	}
];
