import { Component, OnInit } from "@angular/core";
import { SidebarItems } from "./cbas-sidebar.enum";

@Component({
  selector: "cbas-sidebar",
  templateUrl: "cbas-sidebar.component.html",
  styleUrls: ["./cbas-sidebar.component.scss"]
  // styles: [
  //     `.brand{
  //         height: 12rem !important;
  //         width: 100% !important;
  //         background: url('') no-repeat !important;
  //     }`
  // ]
})
export class CbasSidebarComponent implements OnInit {
  public menu = [];
  constructor() { }

  ngOnInit() {
    this.menu = SidebarItems.getItems;
    // this.menu[2].isActive = true;
  }

  isLinkActive(flag: boolean, ind: number, dLinkFlag: boolean) {
    if (dLinkFlag) {
      if (flag !== null) {
        this.menu[ind].isActive = true;
        this.menu.forEach((m, i) => {
          if (i !== ind) {
            m.isActive = false;
          }
        })
      }
    } else {
      this.menu.forEach((m, i) => {
        m.isActive = false;
      })
    }
  }
}
