import { Pipe, PipeTransform } from "@angular/core";

@Pipe({ name: "tdltype" })
export class TDLTypePipe implements PipeTransform {

  constructor() {
  }

  transform(value: string): string {
    let retval = value;
    switch(value){
      case "ANAL":
        retval = "Analysis";
        break;

      case "MAJR":
        retval = "Major Release";
        break;      

      case "MINR":
        retval = "Minor Release";
        break;     
        
      case "UPGR":
        retval = "Upgrade";
        break;             

    }
    return retval;
  }
}
