/*
 @author : Deloitte
 Service class to perform PermitPeriod related operations.
 */
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Observable, of } from "rxjs";
import { catchError, tap } from "rxjs/operators";
import { TDLComments } from '../../tdl/models/tdl-comments.model';
import { environment } from "./../../../environments/environment";

const httpOptions = {
	headers: new HttpHeaders({ "Content-Type": "application/json" })
};

@Injectable()
export class CommentsService {
	private tdlCommentUrl = environment.apiurl + "/api/v1/tdl";

	constructor(private http: HttpClient) {}

	public updateComment(
		comments: TDLComments
	): Observable<TDLComments> {
		// tslint:disable-next-line:max-line-length
		return this.http
			.post(
				this.tdlCommentUrl + "/" + "comment",
				comments,
				httpOptions
			)
			.pipe(
				tap(_ => this.log("updateComment")),
				catchError(this.handleError<any>("updateComment"))
			);
	}

	public deleteTDLCommentById(
		commentId: number
	): Observable<TDLComments> {
		// tslint:disable-next-line:max-line-length
		return this.http
			.delete(
				this.tdlCommentUrl + "/comment/" + commentId,
				httpOptions
			)
			.pipe(
				tap(_ => this.log("deleteTDLCommentById")),
				catchError(this.handleError<any>("deleteTDLCommentById"))
			);
	}

	/**
	 * Handle Http operation that failed.
	 * Let the app continue.
	 * @param operation - name of the operation that failed
	 * @param result - optional value to return as the observable result
	 */
	private handleError<T>(operation = "operation", result?: T) {
		return (error: any): Observable<T> => {
			// TODO: send the error to remote logging infrastructure
			console.error(error); // log to console instead

			// TODO: better job of transforming error for user consumption
			this.log("${operation} failed: ${error.message}");

			// Let the app keep running by returning an empty result.
			return of(result as T);
		};
	}

	/** Log a Attribute Service message with the MessageService */
	private log(message: string) {
		// this.messageService.add("Profile Service: ${message}");
	}
}
