import { Pipe, PipeTransform } from "@angular/core";

@Pipe({ name: "tdlstatus" })
export class TDLStatusPipe implements PipeTransform {

  constructor() {
  }

  transform(value: string): string {
    let retval = value;
    switch(value){
      case "NEW":
        retval = "Proposed";
        break;

      case "APPR":
        retval = "Approved for LOE";
        break;      

      case "PEND":
        retval = "Pending Approval";
        break;     
        
      case "INPR":
        retval = "In Progress";
        break;     
        
      case "COMP":
        retval = "Completed";
        break;   
        
      case "CLSD":
        retval = "Closed";
        break;          

    }
    return retval;
  }
}
