import { HttpClient, HttpHeaders, HttpBackend } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Router } from "@angular/router";
import { JwtHelperService } from "@auth0/angular-jwt";
import { isNil } from "ramda";
import {
	BehaviorSubject,
	from,
	Observable,
	of,
	Subject,
	Subscription,
	throwError,
	timer
} from "rxjs";
import { catchError, map, tap } from "rxjs/operators";
import { RegistrationUser } from "../components/register/registration.model";
import { Credentials } from "../model/credentials.model";
import { LoginState } from "../model/login-state.model";
import { ProfileUser } from "../model/profile-user.model";
import { VerifyUsername } from "../model/verifyusername.model";
import { environment } from "./../../../environments/environment";
import { VerifyEmail } from "./../model/verifyemail.model";
import { NGXLogger } from "ngx-logger";

const httpOptions = {
	headers: new HttpHeaders({ "Content-Type": "application/json" })
};

@Injectable()
export class AuthService {
	public accessToken: string = null;
	public credentialPromise: Promise<any>;
	private loginState: LoginState = new LoginState();
	public email_verified: boolean = true;
	public loggedIn: BehaviorSubject<boolean>;
	private httpBackClient: HttpClient;

	public jwt_isLoggedIn: boolean = false;
	public jwtHelper = new JwtHelperService();

	private apiVerifyURL = environment.apiurl + "/rtc/applicant/verifyemail";
	private apiForgotUsername = environment.apiurl + "/rtc/applicant/forgotuser";

	private authloginurl = environment.apiurl + "/api/auth/login";
	private tokenRefreshUrl = environment.apiurl  + '/api/auth/token';

	/* session timeout */
	public expiring: Subject<boolean> = new Subject<boolean>();
	private expiringSubscription: Subscription = null;
	private expiredSubscription: Subscription = null;

	constructor(private router: Router, private http: HttpClient, private httpb: HttpBackend, private logger:NGXLogger) {
		this.loggedIn = new BehaviorSubject<boolean>(false);
		this.httpBackClient = new HttpClient(httpb);
	}


	public getLoggedUsername() {
		return this.jwt_getLoggedUsername();
	}

	//************************************************************************************************************** */
	// All JWT related methods
	public jwt_login(credentials: Credentials): Observable<any> {
		return this.http.post(this.authloginurl, credentials, httpOptions);
	}

	public jwt_setAccessToken(accesstoken) {
		localStorage.setItem("access_token_id", accesstoken);
	}

	public jwt_getAccessToken() {
		return localStorage.getItem("access_token_id");
	}

	public jwt_removeAccessToken() {
		localStorage.removeItem("access_token_id");
	}

	public jwt_setRefreshToken(refreshtoken) {
		localStorage.setItem("refresh_token_id", refreshtoken);
	}

	public jwt_getRole() {
		return localStorage.getItem("role_list");
	}

	public jwt_setRole(rolelist) {
		localStorage.setItem("role_list", rolelist);
	}

	public jwt_removeRefreshToken() {
		localStorage.removeItem("refresh_token_id");
	}

	public jwt_getRefreshToken() {
		return localStorage.getItem("refresh_token_id");
	}

	public jwt_refreshToken() {
		return this.http.get(this.tokenRefreshUrl, httpOptions);
	}

	public jwt_getScopes(): any[] {
		let token = localStorage.getItem("access_token_id");
		let scopes = this.jwtHelper.decodeToken(token).scopes;
		return scopes;
	}

	private jwt_getLoggedUsername() {
		let loggedUserName = "";
		let token = localStorage.getItem("access_token_id");
		if(!isNil(token)){
			let dtoken = this.jwtHelper.decodeToken(token);
			if(!isNil(dtoken)){
				loggedUserName = dtoken.fullname;
			}
		}
		return loggedUserName;
	}

	public jwt_hasRole(roleName): boolean {
		let scopes = this.jwt_getScopes();
		for (let i in scopes) {
			if (scopes[i] === roleName) return true;
		}
		return false;
	}

	public jwt_getResourceId() {
		let resourceid = -1;
		let token = localStorage.getItem("access_token_id");
		if(!isNil(token)){
			let dtoken = this.jwtHelper.decodeToken(token);
			if(!isNil(dtoken)){
				resourceid = dtoken.resourceid;
			}
		}
		return resourceid;
	}

	public jwt_getEmployeeId() {
		let employeeID = "-1";
		let token = localStorage.getItem("access_token_id");
		if(!isNil(token)){
			let dtoken = this.jwtHelper.decodeToken(token);
			if(!isNil(dtoken)){
				employeeID = dtoken.employeeid;
			}
		}
		return employeeID;
	}

	public jwt_logout(){
		this.jwt_removeAccessToken();
		this.jwt_removeRefreshToken();
		this.jwt_isLoggedIn = false;
		this.router.navigate(["/"]);
	}

	public jwt_loggedIn() {
		let token = localStorage.getItem("access_token_id");
		if (!token) return false;
		return !this.jwtHelper.isTokenExpired(token);
	}

	public jwt_getTokenExpirationTime(): number {
		let token = localStorage.getItem("access_token_id");
		if(!isNil(token)){
			let expTime: number = this.jwtHelper.decodeToken(token).exp;
			let issuedAtTime: number = this.jwtHelper.decodeToken(token).iat;
			return (expTime - issuedAtTime) * 1000;
		}
		return 0.0;
	  }

	  public jwt_getTokenExpWarnTime(): number {
		let token = localStorage.getItem("access_token_id");
		if(!isNil(token)){
			let expWarnTime: number = this.jwtHelper.decodeToken(token).expWarnTime;
			return Number(expWarnTime) * 60 * 1000;
		}
		return 0.0;
	  }
	//******************************************************************* */

	private handleError<T>(operation = "operation", result?: T) {
		return (error: any): Observable<T> => {
			this.logger.error("auth.service.ts:", "handleError:", error);

			// Let the app keep running by returning an empty result.
			return of(result as T);
		};
	}

	/** Log a Attribute Service message with the MessageService */
	private log(message: string) {
		this.logger.info("auth.service.ts: ${message}");
	}
}
