import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { Observable, of } from 'rxjs';
import { DropDownModel } from '../../../app/components/models/dropdown.model';
import { AcuquisitionFyTwentyModel } from '../../../app/components/models/acquisition-fy-twnety.model';
import { text } from '@angular/core/src/render3';

const httpOptions = {
    headers: new HttpHeaders({ "Content-Type": "application/json" })
}

@Injectable()
export class AcquisitionService {

    constructor(private http: HttpClient) { }

    private getAcquisitionurl = environment.apiurl + '/api/v1/acquisition/essacquisition/';
    private getAcquisitionOfficeurl = environment.apiurl + '/api/v1/acquisition/essacquisition/office/';
    private getAcquistionPrecommitmenturl = environment.apiurl + "/api/v1/acquisition/essacquisition/precommitmentlist/"; //Jas
    private getAcquisitionfy20=environment.apiurl +'/api/v1/acquisition/essacquisition/precommitmentdetails/';
    private getReportStateUrl=environment.apiurl +'/api/useradmin/';

    public getAcquisitionData():Observable<any> {
        return this.http.get(this.getAcquisitionurl, httpOptions)
    }

    public postAcquisitionData(payload,office1, acquisitionType1,version1):Observable<any>  {
        return this.http.post(this.getAcquisitionurl+'updatewithctp/?office='+office1+'&acquisitonType='+acquisitionType1+'&version='+version1, payload, httpOptions);
    }

    public getAcquisitionGridFilter(userId):Observable<any>  {
        return this.http.get(this.getReportStateUrl+'filter/list/?userid='+userId, httpOptions);
    }

    public postAcquisitionGridFilter(payload): Observable<any>  {
        return this.http.post(this.getReportStateUrl+'savefilter/', payload, httpOptions);
    }
    public postAcquisitionDataOptional(payload,office1):Observable<any>  {
        return this.http.post(this.getAcquisitionurl+'updatewithctp/?office='+office1, payload, httpOptions);
    }

    public getAcquisitionOfficeData():Observable<any> {
        return this.http.get(this.getAcquisitionOfficeurl, httpOptions);
    }
    //Jaswanthi
    public getAcquisitionPreCommitmentData():Observable<DropDownModel> {
        return this.http.get<DropDownModel>(this.getAcquistionPrecommitmenturl, httpOptions);
    }

    public getAcquisitionPreCommitmentDataWithOffice(office):Observable<DropDownModel> {
        return this.http.get<DropDownModel>(this.getAcquistionPrecommitmenturl+'?office='+office, httpOptions);
    }

    public getAcquisitionPreCommitmentDataWithAcquisition(office, acquisitonType):Observable<DropDownModel> {
        return this.http.get<DropDownModel>(this.getAcquistionPrecommitmenturl+'?office='+office+'&acquisitonType='+acquisitonType, httpOptions);
    }

    // public getAcquisitionPreCommitmentData():Observable<DropDownModel> {
    //     return this.http.get<DropDownModel>(this.getAcquistionPrecommitmenturl, httpOptions);
    // }


    public getAcquisitionFYTwentyData(office1, acquisitionType1,version1,lineItem1):Observable<AcuquisitionFyTwentyModel> {
            return this.http.get<AcuquisitionFyTwentyModel>(this.getAcquisitionfy20+'?office='+office1+'&acquisitonType='+acquisitionType1+'&version='+version1+'&lineItem='+lineItem1, httpOptions);
    }
    public getAcquisitionCTPData(office1, acquisitionType1,version1,role):Observable<any> {
        return this.http.get(this.getAcquisitionurl+'ctp/?office='+office1+'&acquisitonType='+acquisitionType1+'&version='+version1+'&role='+role, httpOptions);
    }
    public getAcquisitionCTPDataWithOptional(office1,role):Observable<any> {
        return this.http.get(this.getAcquisitionurl+'ctp/?office='+office1+'&role='+role, httpOptions);
    }
    public getAcquisitionRunCalc(ALLOC1):Observable<any> {
        return this.http.get(this.getAcquisitionurl+'runcalc/?runcCalcValue=ALLOC1', httpOptions);
    }
    public getAcquisitionRunCalcRevisdAmt():Observable<any> {
        return this.http.get(this.getAcquisitionurl+'runcalc/?runcCalcValue=AGGSPC', httpOptions);
    }
}