export class SaveSearchCriteria{
    keyword: string;
    taskorder: string;
    name: string;
    wbsCode: string;
    pmDisplay: string;
    status: string;
    clin: string;
    planDateRangeStart: string;
    planDateRangeEnd: string;
    planstart: string;
    planend: string;
    client: string;
    category: string;
    tdlType: string;

    public reset(){
        this.keyword = "";
        this.taskorder = "";
        this.name = "";
        this.wbsCode = "";
        this.pmDisplay = "";
        this.status = "";
        this.clin = "";
        this.planstart = "";
        this.planend = "";
        this.planDateRangeStart = "";
        this.planDateRangeEnd = "";
        this.client = "";
        this.category = "";
        this.tdlType = "";
    }
}