import { HttpClientModule, HTTP_INTERCEPTORS } from "@angular/common/http";
import { NgModule } from "@angular/core";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { BrowserModule } from "@angular/platform-browser";
import { BrowserAnimationsModule } from "@angular/platform-browser/animations";
import { PreloadAllModules, RouterModule } from "@angular/router";
import { CookieService } from "angular2-cookie";
import { LoggerModule, NgxLoggerLevel } from 'ngx-logger';
import { AppComponent } from "./app.component";
import { AppConfig } from "./app.config";
import { ROUTES } from "./app.routes";
import { AuthorizationHelper } from "./authentication/util/authorization.helper.util";
import { CoreModule } from "./core/core.module";
import { ErrorComponent } from "./error/error.component";
import { HomeComponent } from "./home.component";
import { AuthGuard } from "./login/services/auth-guard.service";
import { AuthService } from "./login/services/auth.service";
import { ProfileService } from "./login/services/profile.service";
import { CognitoPhonePipe } from "./login/util/cognito-phone.pipe";
import { JwtUtil } from "./login/util/jwt.util";
import { JwtInterceptor } from './_helpers/jwt.interceptor';
import { ErrorInterceptor } from './_helpers/error.interceptor';
import { SharedModule } from "./shared/shared.module";
import { SubmitPlanComponent } from "./components/submit-plan/submit-plan.component";
import { AcquisitionService } from "./tdl/services/acquisition.service";
import { CurrentRequestReportsComponent } from "./components/current-request-reports/current-request-reports.component";
import { ApproveSpendPlanComponent } from "./components/approve-spend-plan/approve-spend-plan.component";
import { AcquistionsFyTwentyComponent } from './components/acquistions-fy-twenty/acquistions-fy-twenty.component';
import { TabsModule } from 'ngx-bootstrap/tabs';
import { NgxSelectModule } from 'ngx-select-ex';
const APP_PROVIDERS = [AppConfig];


export function loggerCallback(logLevel, message, piiEnabled) {
	console.log("client logging" + message);
}

@NgModule({
	bootstrap: [AppComponent],
	declarations: [
		AppComponent,
		ErrorComponent,
		HomeComponent,
		CognitoPhonePipe,
		SubmitPlanComponent,
		CurrentRequestReportsComponent,
		ApproveSpendPlanComponent,
		AcquistionsFyTwentyComponent
	],
	imports: [
		BrowserModule,
		BrowserAnimationsModule,
		FormsModule,
		ReactiveFormsModule,
		HttpClientModule,
		CoreModule.forRoot({ userName: "User" }),
		LoggerModule.forRoot({serverLoggingUrl: '/api/logs', level: NgxLoggerLevel.TRACE, serverLogLevel: NgxLoggerLevel.ERROR}),
		RouterModule.forRoot(ROUTES, {
			useHash: false,
			preloadingStrategy: PreloadAllModules
		}),
		SharedModule,
		TabsModule.forRoot(),//Jas
		NgxSelectModule
	],
	providers: [
		APP_PROVIDERS,
		AuthGuard,
		AuthService,
		AcquisitionService,
		ProfileService,
		JwtUtil,
		AuthorizationHelper,
		CookieService,
		{
			provide: HTTP_INTERCEPTORS,
			useClass: JwtInterceptor,
			multi: true
		},
		{
			provide: HTTP_INTERCEPTORS,
			useClass: ErrorInterceptor,
			multi: true
		}
	]
})
export class AppModule {}
