import {
	Component,
	Input,
	OnInit,
	ViewEncapsulation,
	SimpleChanges,
	Output,
	EventEmitter,
	OnDestroy
} from "@angular/core";
import { CommentsService } from "../service/comments.service";
import { Subscription } from "rxjs";
import { TDLComments } from '../../tdl/models/tdl-comments.model';
import { OnChanges } from '@angular/core';
import { AuthService } from '../../login/services/auth.service';

declare var jQuery: any;

@Component({
	selector: "[comments-panel]",
	templateUrl: "./comments-panel.template.html",
	styleUrls: ["./comments-panel.style.scss"],
	encapsulation: ViewEncapsulation.None
})
export class CommentsPanel implements OnInit, OnDestroy, OnChanges {
	@Input() objectId;
	@Input() comment;
	@Input() objectType;
	@Input() contextInfo;

	public model: TDLComments;

	@Output() onCommentEdit = new EventEmitter<boolean>();

	showCommentsErrorFlag: boolean;
	alertsComments: Array<Object>;
	busy: Subscription;
	context: any;

	constructor(private commentsService: CommentsService,
		private auth: AuthService) {
		this.alertsComments = [
			{
				type: "warning",
				msg:
					'<span class="fw-semi-bold">Warning:</span> Error: Saving Comments'
			}
		];
		this.showCommentsErrorFlag = false;
	}

	ngOnChanges(changes: SimpleChanges) {
		// tslint:disable-next-line:forin
		for (let propName in changes) {
			if (propName === "comment") {
				let chng = changes[propName];
				if (chng.currentValue) {
					this.model = chng.currentValue;
				}
			}
			if (propName === "contextInfo") {
				let chng = changes[propName];
				if (chng.currentValue) {
					this.context = chng.currentValue;
				}
			}
		}
	}

	ngOnInit() {
	}

	ngOnDestroy() {
		if (this.busy) {
			this.busy.unsubscribe();
		}
	}

	public newComment(){
		let saveTDLId = this.model.tdlId;
		this.model = new TDLComments();
		this.model.tdlId = saveTDLId;
	}

	public updateComment() {
		let that = this;
		this.model.createdBy = this.auth.getLoggedUsername();
		if (this.model.comments != null) {
			this.busy = this.commentsService.updateComment(this.model).subscribe(
					data => {
						if (data) {
							that.model.commentsId = null;
							that.model.comments = "";
							that.onCommentEdit.emit(true);
						}
					},
					error => {
						that.showCommentsErrorFlag = true;
						that.alertsComments = [];
						that.alertsComments.push({ type: "warning", msg: error });
					}
				);
		}
	}
}
