import * as _ from 'lodash';
import { Pipe, PipeTransform } from '@angular/core';
import { BidLCatResourceFilterData } from '../models/bid-lcat-filter.model';
import { isNil } from 'lodash';

@Pipe({
    name: "bidlcatfilterpipe"
})
export class BidLaborCategoryDataFilterPipe implements PipeTransform {
    transform(data: any[], filter: BidLCatResourceFilterData): any {
        let results = data;

        if (data) {
            if (filter) {
                let handler = this;
                // filter by Workstream code
                if (typeof filter.taskAreaCodeFilter !== 'undefined' && filter.taskAreaCodeFilter.length > 0) {
                    results =  _.filter(results, function(item) {
                        let val1:string = item.taskAreaCode;
                        let val2:string = filter.taskAreaCodeFilter;
                        return val1.toUpperCase().indexOf(val2.toUpperCase()) > -1;
                    });
                }
                // filter by labor category
                if (typeof filter.laborCategoryFilter !== 'undefined' && filter.laborCategoryFilter.length > 0) {
                    results =  _.filter(results, function(item) {
                        let val1:string = isNil(item.laborCategory) ? "" : item.laborCategory;
                        let val2:string = filter.laborCategoryFilter;
                        return val1.toUpperCase().indexOf(val2.toUpperCase()) > -1;
                    });
                }
            }
        }
        return results;
    }
// tslint:disable-next-line:eofline
}