import { Injectable, Optional  } from '@angular/core';
import { TDLResourceView } from '../tdl/models/tdlresourceview.model';
import { SaveSearchCriteria } from '../tdl/models/searchcriteria.model';
import { TDLView } from '../tdl/models/tdlview.model';

export class ShareDataServiceConfig {
  userName = 'App User';
}

let nextId = 1;

@Injectable()
export class ShareDataService {
    id = nextId++;
    idArray: number[];
    previousRoute: string;
    breadcrumb: string;
    acceptanceFlag: string;
    qatabfrom: string;
    submitted: string;
    createdDate: string;
    projectManager: string;
    selResourceName: string;
    selTDLName: string;
    selWeekEnding: Date;

    searchCriteria: SaveSearchCriteria = new SaveSearchCriteria();

    phase: string;

    client: string;
    tdlname: string;
    planStartDt: Date;
    budgethours: Number;
    planhours: number;
    budgetcost: number;
    status: string;

    tdlNodeId: number;

    loggedUser: string;

    tdlResources: TDLResourceView[];
    tdlData: TDLView[];


    private _userName = 'User';

    constructor(@Optional() config: ShareDataServiceConfig) {
        if (config) {
            this._userName = config.userName;
        }
        this.reset();

    }

    get userName() {
        // Demo: add a suffix if this service has been created more than once
        const suffix = this.id > 1 ? ` times ${this.id}` : '';
        return this._userName + suffix;
    }

    reset() {
        this.acceptanceFlag = '';
        this.previousRoute = '';
        this.breadcrumb = '';
        this.qatabfrom = '';
        this.submitted = '';
        this.createdDate = '';
        this.idArray = [];

        this.client = "";
        this.tdlname = "";
        this.status = "";

        this.tdlResources = [];
        this.tdlData = [];

        this.selResourceName = "";
        this.tdlNodeId = -1;

        this.searchCriteria.reset();
    }
}
