import { Pipe, PipeTransform } from "@angular/core";

@Pipe({ name: "resteam" })
export class ResourceTeamPipe implements PipeTransform {

  constructor() {
  }

  transform(value: string): string {
    let retval = value;
    switch(value){
      case "TPMO":
        retval = "PMO";
        break;

      case "TTST":
        retval = "Testing";
        break;      

      case "TPMT":
        retval = "Project Managers";
        break;     
        
      case "TECH":
        retval = "Technical";
        break;     
        
      case "TARC":
        retval = "Architectural";
        break;   
        
      case "TOPR":
        retval = "Operations";
        break; 
        
    case "TBAT":
        retval = "Business Analysts";
        break;  
        
    case "TOCM":
        retval = "OCM/Training";
        break;             

    }
    return retval;
  }
}
