import * as moment from 'moment';

export class ProfileUser
{
    constructor() {
        this.DT_FIRST_NAME = "";
        this.DT_LAST_NAME = "";
        this.DT_MIDDLE_INITIAL = "";
    }
    DT_PREFIX: string; // new field added for update
    DT_FIRST_NAME: string;
    DT_MIDDLE_INITIAL: string;
    DT_LAST_NAME: string;
    DT_SUFFIX: string; // new field added for update
    DT_BIRTHDAY: string;
    DT_SSN4_NO: string;
    DT_SSN: string;
    DT_PROFILE_ID: string;
    DT_MAILING_ADDR1: string;
    DT_MAILING_ADDR2: string;
    DT_MAILING_CITY_TOWN_PO: string;
    DT_MAILING_ZIP1: string;
    DT_MAILING_ZIP2: string;      
    DT_MAILING_STATE: string;
    DT_EMAIL_ADDRESS: string;
    DT_PHONE_NUMBER: string;  

    fromDB(data: any) {
        // for some reason, these two fields are being stored
        // as string literal "undefined"
        if(data[0].Address2 === "undefined") {
            data[0].Address2 = "";
        }
        if(data[0].Zip2 === "unde") {
            data[0].Zip2 = "";
        }
        this.DT_PREFIX = data[0].Prefix;
        this.DT_FIRST_NAME = data[0].FirstName;
        this.DT_MIDDLE_INITIAL = data[0].MiddleInitial;
        this.DT_LAST_NAME = data[0].LastName;
        this.DT_SUFFIX = data[0].Suffix;
        this.DT_BIRTHDAY = data[0].BirthDate;
        this.DT_SSN4_NO = data[0].SSN4;
        this.DT_SSN = data[0].SSN;
        this.DT_MAILING_ADDR1 = data[0].Address1;
        this.DT_MAILING_ADDR2 = data[0].Address2;
        this.DT_MAILING_CITY_TOWN_PO = data[0].City;
        this.DT_MAILING_STATE = data[0].State;
        this.DT_MAILING_ZIP1 = data[0].Zip1;
        this.DT_MAILING_ZIP2 = data[0].Zip2;
        this.DT_EMAIL_ADDRESS = data[0].EmailAddress;
        this.DT_PHONE_NUMBER = data[0].PhoneNumber;
        this.DT_BIRTHDAY = this.birthdayMMDDYYYY(this.DT_BIRTHDAY);
        this.DT_PHONE_NUMBER = this.phoneWithDashes(this.DT_PHONE_NUMBER);
    }
    combinedAddress():string {
        return this.DT_MAILING_ADDR1 + "|" + 
            this.DT_MAILING_ADDR2 + "|" + 
            this.DT_MAILING_CITY_TOWN_PO + "|" +
            this.DT_MAILING_STATE + "|" + 
            this.DT_MAILING_ZIP1 + "|" + 
            this.DT_MAILING_ZIP2;
    }
    phoneWithDashes(phone:string):string {
        if(!phone) return "";
        return phone.substring(0,3) + '-' +
            phone.substring(3,6) + '-' +
            phone.substring(6); 
    }
    phoneWithoutDashes(phone: string):string {
        if(!phone) return "";
        return phone.replace(new RegExp('-', 'g'), '');
    }
    cognitoPhone(phone: string): string { 
        if(!phone) return "";
        return '+1' + this.phoneWithoutDashes(phone);
    }
    birthdayYYYYMMDD(dateString: string) {
        return moment(dateString).format('YYYY-MM-DD');
    }
    birthdayMMDDYYYY(dateString: string) {
        return moment(dateString).format('MM/DD/YYYY');
    }
    ssnWithoutDashes():string {
        if(!this.DT_SSN) return "";
        return this.DT_SSN.replace(new RegExp('-', 'g'), '');
    }
    ssnTrimLeadingZeroes():string {
        if(!this.DT_SSN) return "";
        let tempSSN = this.DT_SSN.trim();
        tempSSN = this.DT_SSN.replace(new RegExp('-', 'g'), '');
        return tempSSN.replace(/^0+/, '');
    }
    last4SSN() {
        return this.DT_SSN.substring(this.DT_SSN.length - 4)
    }
}
 