import { Pipe, PipeTransform } from "@angular/core";

@Pipe({ name: "clinPipe" })
export class CLINCodePipe implements PipeTransform {

  constructor() {
  }

  transform(value: string): string {
      if (value === "KLAB") {
        return("LAB");
      } else if (value === "KSYE") {
        return ("SYE");
      } else if (value === "KFFP") {
        return ("FFP");
      } else if (value === "KOTH") {
        return ("OTH");
      }else {
        return ("-");
      }
  }
}
