import { isNil } from 'ramda';
import { Pipe, PipeTransform } from "@angular/core";

@Pipe({ name: "wbsPipe" })
export class WBSCodePipe implements PipeTransform {

  constructor() {
  }

  transform(value: string): string {
      if(!isNil(value)){
        value = value.replace(/\./g, '-');
      }
      return value;
  }
}
