import { isNil } from "ramda";
import { LocalStorageService } from "ngx-webstorage";
import { Router } from "@angular/router";
import { Subscription } from "rxjs";
import { AuthService } from "./login/services/auth.service";
import { Component, OnInit, OnDestroy, HostListener } from "@angular/core";
import * as moment from "moment";
import { Title } from "@angular/platform-browser";
import { LoginState } from "./login/model/login-state.model";
import { NGXLogger } from "ngx-logger";
import { LicenseManager } from "ag-grid-enterprise";

declare var window: any;
declare var jQuery: any;

@Component({
  selector: "app-root",
  templateUrl: "./app.template.html",
  styleUrls: ["./app.style.scss"],
  providers: [LocalStorageService]
})
export class AppComponent implements OnInit, OnDestroy {
  subscription: Subscription;
  msg: string = "Your session is about to expire.  Click close to continue.";
  title: string = "Session Timeout";

  public isUserLoggedIn: boolean;
  public loggedInUserName: string;

  constructor(
    public auth: AuthService,
    private storage: LocalStorageService,
    private titleService: Title,
    private router: Router,
    private logger: NGXLogger
  ) {
    this.logger.debug("or: Application Initialized");
    LicenseManager.setLicenseKey(
      "[TRIAL]_10_August_2019_[v2]_MTU2NTM5NTIwMDAwMA==53f1292ef78d2de19c8266ac2435ead81234"
    );
  }

  addDateValidators() {
    //has no future date
    window.Parsley.addValidator("maxdate", {
      validateString: function(value, requirement) {
        let timestamp = Date.parse(value),
          maxTs = Date.parse(requirement);
        if (isNaN(timestamp)) return true;
        return timestamp < maxTs;
      },
      messages: {
        en: "Error: Future date is not allowed"
      }
    });

    window.Parsley.addValidator("olderthan365days", {
      validateString: function(value, xdays) {
        let allowedCondition = true;
        let entrydate = moment(value);
        let today = moment();
        let diff = today.diff(entrydate);
        if (diff < 365) {
          allowedCondition = false;
        }
        return allowedCondition;
      },
      messages: {
        en: "Error: Can't enter a date older than 365 days"
      }
    });

    window.Parsley.addValidator("validdate", {
      validateString: function(value, requirement) {
        var timestamp = Date.parse(value),
          maxTs = Date.parse(requirement);
        if (!isNaN(timestamp)) return true;
        return false;
      },
      messages: {
        en: "Error: Invalid Date"
      }
    });

    window.Parsley.addValidator("pastyears", {
      validateString: function(value, requirement) {
        let allowedCondition = true;
        let curyear = moment().year();
        let diff = curyear - value;
        if (diff < 0 || diff > requirement) {
          allowedCondition = false;
        }
        return allowedCondition;
      },
      messages: {
        en: "Error: Year not allowed"
      }
    });
  }

  ngOnInit() {
    this.addDateValidators();
    // this.subscription = this.auth
    // 	.amp_isAuthenticated()
    // 	.subscribe(result => {});
    this.auth.expiring.subscribe((x: boolean) => {
      // session is about to time out, display warning popup
      if (x) {
        jQuery("#app-popup").modal("show");
      } else {
        jQuery("#app-popup").modal("hide");
      }
    });
    this.setTitle("CBASDemo");

    let context = this;
    window.addEventListener("beforeunload", function(e) {
      if (context.subscription !== undefined) {
        context.subscription.unsubscribe();
        // context.auth.expiring.unsubscribe();
        context.onClickLogout();
      }
    });

    this.isUserLoggedIn = this.isLoggedIn();

    if (this.isUserLoggedIn) {
      this.loggedInUserName = this.getLoggedUser();
    }
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
    // this.auth.expiring.unsubscribe();
    this.onClickLogout();
  }

  public clearLocalStorageContent(modPrefix) {}

  public onClickLogout() {
    this.auth.jwt_logout();
    this.storage.clear("LOGGEDUSERID");
    this.storage.clear("LOGGEDUSEREMAIL");
    this.storage.clear("SearchType");
    this.storage.clear("SearchResource");
    this.isUserLoggedIn = this.isLoggedIn();
    this.loggedInUserName = undefined;
  }

  public onClose() {
    // this.auth.jwt_resetSessionTimeout();
  }

  public canEditProfile() {
    return this.isLoggedIn();
  }

  public isLoggedIn() {
    // let status: LoginState = this.auth.amp_getLoginState();
    // if (this.auth.jwt_loggedIn()) {
    // 	return true;
    // }
    // return false;

    return this.auth.jwt_loggedIn() ? true : false;
  }

  public isMyProfileShown() {
    return this.isLoggedIn();
  }

  public isMyHomePageShown() {
    return this.isLoggedIn();
  }

  public setTitle(newTitle: string) {
    this.titleService.setTitle(newTitle);
  }

  public closeAnnouncement() {
    jQuery("#announcement").hide();
  }

  public getLoggedUser() {
    return this.auth.getLoggedUsername();
  }
}
