  // currencyFormatter() {
  //   return "\xA3" +
  // }

  currencyFormatter(params) {
    let i = this.formatNumber(params.value);
    return i !== '' ? ("\x24" + i) : ''
    // return "\x24" + this.formatNumber(params.value);
  }
  formatNumber(number) {
    // (number.value !== null && number.value !== undefined) ? Math.floor(number.value) : ''
    return (number !== null && number !== undefined) ? Math.floor(number)
      .toString()
      .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,") : '';
  }
  formatDecimal(number) {
    console.log(number.value, 'number');

    return (number.value !== null && number.value !== undefined) ? Math.floor(number.value) : '';
  }
  bracketsFormatter(params) {
    if (params.value !== null && params.value[0] === '-') {
      return params.value[0] !== '-' ? this.formatNumber(params.value) : "(" + params.value.substr(1) + ")";
    } else {
      return params.value === null ? '' : '$' + params.value;
    }
  }

